import uniLoadMore from '../../../components/uni-load-more.vue';
export default{ 
	 components: {//2注册组件
		uniLoadMore
	},
	data(){
		return{
			indicatorDots: true,
			autoplay: true,
			interval: 4000,
			duration: 800,
			circular:true,
			banner_list:[],
			page:1,
			contentList:[],
			allPages:'',
			loadingText: '加载中...',
			loadingType: 0,//定义加载方式 0---contentdown  1---contentrefresh 2---contentnomore
			contentText: {
				contentdown:'上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			}
		}
	},
	onReachBottom:function(){
		let _this=this;
		_this.events1();
	},
	created(){
		let _this=this;
	},
	mounted(){
		let _this=this;
		//baner
		_this.getBanner();
		_this.events1();
	},
	methods:{
		goMsg(url,id){
			if(id!=''){
				url = url+'?id='+id
			}
			uni.navigateTo({
				url:url
			})
		},
		
		//banner
		getBanner:function(){
			let _this=this;
			let data={
				id:6,
			};
			_this.$axios(_this.$baseUrl.banner,data).then(res =>{
				if(res.data.status==1){
					_this.banner_list=res.data.result.banner;
				}
			},(error) =>{
				
			})
		},
		// 列表
		events1:function(){
			let _this=this;
			let data={
				type:6,
				page:_this.page,
				Hot:_this.Hot,
				New:_this.New,
			};
			if (_this.loadingType !== 0) {//loadingType!=0;直接返回
				return false;
			}
			_this.loadingType = 1;
			uni.showNavigationBarLoading()
			_this.$axios(_this.$baseUrl.events1,data).then(res =>{
				if(res.data.status==1){
					if (res.data.result.articles == null ||res.data.result.articles==undefined ||res.data.result.articles=='' ) {//没有数据
					    _this.loadingType = 2;
					    uni.hideNavigationBarLoading();//关闭加载动画
					    return;
					}
					 _this.page++
					_this.contentList = _this.contentList.concat(res.data.result.articles)
					_this.allPages = res.data.result.pages;
					_this.loadingType = 0;//将loadingType归0重置
					uni.hideNavigationBarLoading();//关闭加载动画
				}else{
					_this.loadingType = 2;
					uni.hideNavigationBarLoading();//关闭加载动画
					return;
				}
			}).catch(error =>{
				
			})
		}
		
	}
}
